/**
 * @name autobind
 * @type {function}
 * @param target
 * @param propertyKey
 * @param descriptor
 * @returns {any}
 * @description Method decorator that binds it to the instance of the class it is defined on
 */
export function alias(...aliases: string[]) {
  return (target: any, propertyKey: string, descriptor: PropertyDescriptor) => {
    const method = descriptor.value;

    if (typeof method !== 'function') {
      throw new Error(`@alias decorator can only be applied to methods not: ${typeof method}`);
    }

    return {
      configurable: descriptor.configurable,
      enumerable: descriptor.enumerable,
      get() {
        aliases.forEach(alias => {
          if (!this.hasOwnProperty(alias)) {
            Object.defineProperty(this, alias, {
              configurable: true,
              writable: true,
              value: method,
              enumerable: true
            });
          }
        });
        return method;
      }
    };
  };
}